:root {
  --main-color: #b59d5a;
  --primary-accent: rgba(78, 86, 50, 0.86);
  --brand-grey: #676767;
  --white: #fff;
  --transition-duration: 250ms;
  --transition-cubic: cubic-bezier(0.4, 0, 0.2, 1);
}

.tempNav {
  display: flex;
  gap: 8px;
}

body {
  margin: 0;

  color: var(--white);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: calc(18 / 14);
}

a {
  color: var(--white);
  text-decoration: none;
}

h1,
h2,
h3,
h4,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

button {
  font-family: inherit;
  cursor: pointer;
  border: none;
}
.container {
  margin-right: auto;
  margin-left: auto;
  min-width: 280px;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 100;
    margin-right: 100;
  }
}
